.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  padding: 10px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.wrapper {
  border-radius: 9px;
  padding: 4px;
  border: 1px solid #222;
  margin: auto;
  width: 600px;
  word-wrap: break-word;
}

@media screen and (max-width:760px) {
  .wrapper {
    margin: 2px;
    width: 100%;
  }
}

.whois-title {
  padding: 3px;
  margin: -4px;
  background: steelblue;
  border: 2px solid saddlebrown;
}

input[type=text] {
  height: 32px;
  border-radius: 7px;
  font-weight: bolder;
  width: 30%;
}

input[type=submit] {
  height: 34px;
  border-radius: 4px;
  margin-left: 4px;
  background: rgb(5, 126, 173);
  font-weight: bolder;
}


